<template>
  <select v-model="selectedOption" class="dropdown-list" @input="event => { $emit('input', event.target.value) }">
    <option v-for="(option, i) in options" :key="i" :value="option" >{{ option }}</option>
  </select>
</template>

<script>
export default {
  name: 'DropdownList',
  data () {
    return {
      selectedOption: null
    }
  },
  props: {
    value: null,
    options: {
      type: Array,
      required: true
    }
  },
  mounted () {
    this.selectedOption = this.value
  },
  watch: {
    value: function (newValue) {
      this.selectedOption = newValue
    }
  }
}
</script>

<style lang="scss">
.dropdown-list {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: #ddd url('../assets/images/chevron-down.svg') no-repeat center right 10px;
  border-radius: 0px;
  border: none;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  font-size: 16px;
  padding: 15px;
  position: absolute;
  right: 0px;
  top: 0px;
  width: 200px;
  z-index: 6;

  &:focus,
  &:active {
    outline: none;
  }
}
</style>
